import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { IApiPath, IIpaasApiDefinition, IOrganizationData } from '../ipaas-base-apis/ipaas-base-apis.types';

@Injectable({
  providedIn: 'root'
})
export class RxMulesoftApisService {
  private readonly apiDefinitionUrl = '/api/com.bmc.dsm.ipaas-mulesoft/rx/application/ipaas/mulesoft';

  constructor(private httpClient: HttpClient) {}

  deleteApiDefinition(apiId: string): Observable<any> {
    return this.httpClient.delete(`${this.apiDefinitionUrl}/apidefinition/${apiId}`);
  }

  createApiDefinition(apiDefinition: IIpaasApiDefinition): Observable<any> {
    return this.httpClient.post(`${this.apiDefinitionUrl}/apidefinition`, apiDefinition);
  }

  getApiDefinition(apiId: string): Observable<IIpaasApiDefinition> {
    return this.httpClient.get<IIpaasApiDefinition>(`${this.apiDefinitionUrl}/apidefinition/${apiId}`);
  }

  editApiDefinition(apiDefinition: IIpaasApiDefinition): Observable<any> {
    return this.httpClient.put(`${this.apiDefinitionUrl}/apidefinition/${apiDefinition.id}`, apiDefinition);
  }

  getOrganizationsAndEnvironments(): Observable<IOrganizationData[]> {
    return this.httpClient.get<IOrganizationData[]>(`${this.apiDefinitionUrl}/organization-info`);
  }

  getApiPathDefinitions(organization: string, environment: string): Observable<IApiPath> {
    return this.httpClient.get<IApiPath>(
      `${this.apiDefinitionUrl}/api-specs/${encodeURIComponent(organization)}/${encodeURIComponent(environment)}`
    );
  }
}
